import React, { useState, useEffect } from "react";

// react-bootstrap components
import {
    Badge,
    Button,
    Card,
    Navbar,
    Nav,
    Table,
    Container,
    Row,
    Form,
    Col,
} from "react-bootstrap";
import { Label } from "reactstrap";
import { Link, NavLink } from "react-router-dom";
import userAPI from "API/UserAPI";
import { useHistory } from "react-router-dom";
import { isValid } from "date-and-time";


function Register() {

    const [userName, setUserName] = useState('');
    const [fullName, setFullName] = useState('');
    const [password, setPassword] = useState('');
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');
    const [dateOfBirth, setDateOfBirth] = useState('');

    const [field,setFields] = useState('');
    const [errors,setErrors] = useState('');

    const [listUsers, setListUsers] = useState([]);

    const history = useHistory();

    useEffect(() => {
        getListUsers();
      }, []);

    const getListUsers = () => {
        userAPI.getAll().then((response) => {
          setListUsers(response.data);
        });
    }

    const handleValidation = () => {
        let fields = field;
        let formIsValid = true;
        let errors = {};
        
        //UserName
        if (!fields["userName"]) {
            formIsValid = false;
            errors["userName"] = "Username không được bỏ trống!";
        }
        if (typeof fields["userName"] !== "undefined") {
            if (!fields["userName"].match(/^[a-zA-Z]+$/)) {
                formIsValid = false;
                errors["userName"] = "Chỉ bao gồm chữ cái thường và chữ cái Hoa!";
            }
        }
        if(listUsers.map((user) => user.userName) === userName){
            errors["userName"] = "Username đã tồn tại!";
        }

        //Email
        if (!fields["email"]) {
            formIsValid = false;
            errors["email"] = "Email không được bỏ trống!";
        }

        setErrors({errors : errors});
        return formIsValid;
    }

    const saveUserRegister = () => {
        const userRegister = {userName, fullName, password, email, phone, dateOfBirth};
        if(handleValidation()){
            userAPI.register(userRegister).then((response) => {
                alert('Register successfully!');
                history.push('/admin/login');
            })
        }else{
            alert('Not Fail register!');
        }
    }

    const handleChange = (field, e) => {
        let fields = field;
        fields[field] = e.target.value;
        setFields({ fields });
    }

    return (
        <>
            <fieldset className="border p-2">
                <legend className="scheduler-border" id="formUser">Nhập thông tin thành viên</legend>
                <form id="formUpdateOrCreate">
                    <div className="form-group row row-item">
                        <div className="div-30">
                            <label htmlFor="inputCode" className="col-form-label">Username:</label>
                        </div>
                        <div className="div-70">
                            <input type="text" value={field["userName"]} onChange={(e)=>handleChange().bind(this, "userName")} className="form-control" id="inputCode" placeholder="Nhập vào username..." />
                            <span style={{color:"red"}}>{errors["userName"]}</span>
                        </div>
                    </div>
                    <div className="form-group row row-item">
                        <div className="div-30">
                            <label htmlFor="inputFullName" className="col-form-label">Họ và tên:</label>
                        </div>
                        <div className="div-70">
                            <input type="text" value={fullName} className="form-control" onChange={(e) => setFullName(e.target.value)} id="inputFullName" placeholder="Nhập vào họ và tên..." />
                        </div>
                    </div>
                    <div className="form-group row row-item" >
                        <div className="div-30">
                            <label htmlFor="inputPassword" className="col-form-label" >Mật khẩu:</label>
                        </div>
                        <div className="div-70">
                            <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} className="form-control" id="inputPassword" placeholder="Nhập vào mật khẩu..." />
                        </div>
                    </div>
                    <div className="form-group row row-item">
                        <div className="div-30">
                            <label htmlFor="inputEmail" className="col-form-label">Email:</label>
                        </div>
                        <div className="div-70">
                            <input type="email" value={field["email"]}  onChange={(e)=>handleChange().bind(this, "email")} className="form-control" id="inputEmail" placeholder="Nhập vào email..." />
                            <span style={{color:"red"}}>{errors["email"]}</span>
                        </div>
                    </div>
                    <div className="form-group row row-item">
                        <div className="div-30">
                            <label htmlFor="inputPhone" className="col-form-label">Số điện thoại:</label>
                        </div>
                        <div className="div-70">
                            <input type="number" value={phone}  onChange={(e) => setPhone(e.target.value)} className="form-control" id="inputPhone" placeholder="Nhập vào số điện thoại..." />
                        </div>
                    </div>
                    <div className="form-group row row-item">
                        <div className="div-30">
                            <label htmlFor="inputBirthday" className="col-form-label">Ngày sinh:</label>
                        </div>
                        <div className="div-70">
                            <input type="text" value={dateOfBirth}  onChange={(e) => setDateOfBirth(e.target.value)} className="form-control" id="inputBirthday" placeholder="Nhập vào ngày sinh..." />
                        </div>
                    </div>
                    <ul className="nav justify-content-end">
                        <div>
                            <button type="button" onClick={(e) => saveUserRegister(e)} className="btn-sm btn-fill btn-info btn-save-student">Lưu thay đổi</button>
                            <button className="btn-sm btn-fill btn-secondary" data-dismiss="modal"><Link to="/admin/manager-user">Hủy</Link></button>
                        </div>
                    </ul>
                </form>
            </fieldset>
        </>
    );
}

export default Register;